import ajax from '@/services/ajax';
import api from '@/services/api';

/*   */
export function queryCreditCard(data) {
    return ajax.postAfterLoginQuery('123456', data, api.queryCreditCard);
}
/*   */
export function queryCredit(data) {
    return ajax.postAfterLoginQuery('123456', data, api.queryCredit);
}

/*   */
export function queryCurrentBill(data) {
    return ajax.postAfterLoginQuery('123456', data, api.queryCurrentBill);
}


