<template>
	<div class="credit-manage-card-set">
		<div class="cell-part">
			<mt-cell title="" :value="card"></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('CreditManageCardSetRename')" title="" :value="detail.name || ''" is-link></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('CreditManageCardSetCost')" title="" :value="detail.msg" is-link></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('QuickLoss')" title="" is-link></mt-cell>
			<mt-cell @click.native="toSet('PatchCard')" title="" is-link></mt-cell>
		</div>
		<div class="delete-button">
			<a @click="submit" href="javascript:;"></a>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/credit/manage';

export default {
	data() {
		return {
			card: '',
			detail: {
				name: '',
				msg: ''
			}
		};
	},
	methods: {
		toSet(name) {
			this.$router.push({name: name, params: {card: this.card}});
		},
		submit() {
			var self = this;
			let card = self.card.substring((self.card.length - 4), self.card.length);
			MessageBox({
			 	title: '',
			  	message: `${card}`,
			  	showCancelButton: true,
			  	confirmButtonText: ''
			}).then(action => {
				if(action === 'confirm'){
					self.deleteCredit();
				}
			});
		},
		deleteCredit() {
			service.deleteCredit()
			.then((data) => {
				this.$router.push({name: 'CreditManageCardSetSuccess', params: {type: 'delete'}});
			}, (err) => {
				console.log(err);
			});
		}

	},
	created() {
		this.card = this.$route.params.card;
	}
};

</script>

<style lang="scss">
	
.credit-manage-card-set{
	.delete-button{
		margin-top: 80px;
		padding: 24px 0;
		text-align: center;
		background-color: #fff;
		a{
			color: #FF511A;
			font-size: 15px;
		}
	}
}

</style>


