<template>
	<div class="set-service-update">
		<div class="field-part">
			<mt-cell class="mint-select" title="" is-link>
	            <select class="mint-field-core" v-model="detail.sendType">
	                <option value=""></option>
	                <option value="1"></option>
	            </select>
	        </mt-cell>
	        <mt-field v-model="detail.mail" label="" placeholder=""></mt-field>
		</div>
		<div class="field-part">
	        <mt-cell class="mint-select" title="" is-link>
	            <select class="mint-field-core" v-model="detail.addressType">
	                <option value=""></option>
	                <option value="1"></option>
	            </select>
	        </mt-cell>
	        <mt-field v-model="detail.address1" label="" placeholder=""></mt-field>
	        <mt-field v-model="detail.address2" label="" placeholder=""></mt-field>
		</div>
		<div class="field-part">
        	<cell-password :options=" cellPassword "></cell-password>
		</div>
        <div class="field-warn text-right">
        	<a @click="toResetPwd" href="javascript:;" class="forget-pwd"></a>
        </div>
        <div class="button-bottom">
			<mt-button @click.native="update" type="primary" size="large"></mt-button>
        </div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			detail: {
				sendType: '',
				mail: '',
				addressType: '',
				address1: '',
				address2: ''
			},
			cellPassword: {
                title: '',
                rule: 'number' // number or mix
            }
		};
	},
	methods: {
		toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
		update() {
			this.updateSetService();
		},
		updateSetService() {
			service.updateSetService()
			.then((data) => {
				this.$router.push({name: 'CreditManageSetServiceSuccess'});
			}, (err) => {
				console.log(err);
			});
		}
	}
};

</script>

<style lang="scss">
	
.set-service-update{
	.mint-cell-title,{
		width: 3.6rem;
	}
	.mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .forget-pwd{
		color: #308cf8;
		font-size: 13px;
	}
}

</style>


