<template>
    <div class="consume">
        <index ref="child" options="options">
            <template slot="info">
                <p>500()</p>
            </template>
            <div class="field-action" slot="btn">
                <mt-button type="default" size="large" @click="doApply"></mt-button>
            </div>
            <template slot="process">
                <p>
                    
                </p>
                <p>
                    
                </p>
            </template>
            <template slot="warn">
                <p>
                    
                </p>
                <p>
                    1369121824
                </p>
                <p>
                    2
                </p>
            </template>
        </index>
    </div>
</template>

<script>
    import Index from '../public';
    export default {
        name: 'consume',
        components: {
            Index
        },
        data() {
            return {
                msg: ''
            };
        },
        methods: {
            doApply() {
                let _data = Object.assign({}, this.$refs.child.formData);
                if (this.$validate.isEmpty(_data.account)) {
                    this.$msg('');
                    return false;
                }
                this.$router.push({ name: 'CreditConsumeList' });
            }
        }
    };

</script>


