<template>
	<div class="credit-manage-auto-repay">
		<div class="cell-part">
			<mt-cell @click.native="toSet" title="" :value="detail.isOpen ? '' : ''" is-link></mt-cell>
		</div>
		<div v-if="!detail.isOpen" class="head-hint"></div>
		<div class="cell-part" v-if="detail.isOpen">
			<div class="head-hint"></div>
			<mt-cell title="" :value="detail.account"></mt-cell>
			<mt-cell title="" :value="detail.type"></mt-cell>
		</div>
		<mt-button type="primary" @click.native="detail.isOpen = !detail.isOpen"></mt-button>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			detail: {
				isOpen: false,
				account: '',
				type: ''
			}
		};
	},
	methods: {
		toSet() {
			let name = this.detail.isOpen ? 'CreditManageAutoRepayOpen' : 'CreditManageAutoRepayNotOpenSet';
			this.$router.push({name: name});
		},
		autoRepaySearch() {
			service.autoRepaySearch()
			.then((data) => {
			}, (err) => {
				console.log(err);
			});
		}
	},
	created() {
		this.autoRepaySearch();
	}
};

</script>

<style lang="scss">
	
.credit-manage-auto-repay{

}

</style>


